import { ComponentStylingModel, ComponentStylingPropertiesControlComponent, genericPropertiesControlFor } from '@anglr/dynamic/layout-editor';
import { ButtonModel } from './button.model';
/**
 * Button layout metadata
 */

export class ButtonLayoutEditorMetadata {
  //######################### constructor #########################
  constructor() {
    //######################### public properties - implementation of LayoutEditorMetadataDescriptor #########################

    /**
     * @inheritdoc
     */
    this.metaInfo = {
      name: 'Button',
      description: 'Simple button',
      group: 'Buttons',
      optionsMetadata: {
        propertiesMetadata: [{
          modelType: ComponentStylingModel,
          propertiesControls: [ComponentStylingPropertiesControlComponent]
        }, {
          modelType: ButtonModel,
          propertiesControls: [genericPropertiesControlFor(['buttonCssClass', 'text', 'disabled'])]
        }]
      },
      defaultOptions: {
        text: 'Click me',
        disabled: false,
        buttonCssClass: 'btn btn-primary'
      }
    };
    Object.freeze(this);
  }

}